//     // while(true)  제너레이터에서만 사용가능한 문법
//     // 클릭이벤트를 횟수 제한도 가능, 반복 (for문) 사가에서 동작하지 않아도 리듀서는 동작 -> 별개
//     while(true){
//         yield take(HELLO_SAGA);
//         console.log('hello saga');
//     }
//     // 비동기 요청, 타이머 넣어도 되고
// }


function* watchLogin() {
    while(true){

        yield take(LOG_IN);
        yield put ({
            type: LOG_IN_SUCCESS,
        });
    }
}

function* watchSignUp(){

}

export default function* userSaga() {
    yield all([
        watchLogin(),
        watchSignUp(),
    ]);

}